import React from 'react';
import axios from 'axios';
import stringify from 'qs-stringify';



const getItemDescription = (uniqueId) => {
		let item;
       axios.get(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php?item=`+ uniqueId)
      .then((response) => {

               item =  response.data.success ? response.data : "error";
               console.log(response.data.success);

  })
      console.log(item);
  	return item;    
}



function ItemDescription(props) {


 const uniqueId = props.match.params.uid;
 const item = getItemDescription(uniqueId);


	return (<div className='ItemDescription'>
				{item}
				
			</div>
		)
}

export default ItemDescription;